<template>
    <!--es6-->
    <div class="es6Test">
        <h1>es6</h1>
        <section>
            <h3>1.</h3>
            <ol>
                <li>1.arguments</li>
                <li>2.prototype(new)</li>
                <li>3.thisthis</li>
            </ol>
        </section>
        <section>
            <h3>2.es6  class</h3>
            <h5>class</h5>
            <p>JavaScript</p>
            <code>
                //
                function Person(name,age) {
                    this.name = name;
                    this.age = age;
                }
                Person.prototype.sayInfo = function () {
                    console.log(`${this.name}${this.age}`)
                }
                const liLei = new Person('LiLei',20)
                liLei.sayInfo()
                //LiLei20
            </code>
            <p>javaes6</p>
            <code>
                class Person{ //Person
                    constructor(name,age){ //constructor
                    this.name = name;  //this
                    this.age = age;
                }
                sayInfo(){
                    console.log(`${this.name}${this.age}`)
                    }
                }
                const liLei = new Person('LiLei',21)
                liLei.sayInfo()
            </code>
            <p>ES6</p>
            <code>
                console.log(typeof Person);//function
                console.log(Person===Person.prototype.constructor);//true
            </code>
            <h5>class</h5>
        </section>

    </div>
</template>

<script>

    import { mavonEditor } from 'mavon-editor'
    import 'mavon-editor/dist/css/index.css'

    export default {
        name: "es6Test",
        data() {
            return {
                config:{
                    mode:1  //
                },
                arrowContent:''
            }
        },
        methods: {
            arrowFun(){

            },
            esClassTest(){
                //
                const commonWrite = function () {
                    function Person(name,age) {
                        this.name = name;
                        this.age = age;
                    }
                    Person.prototype.sayInfo = function () {
                        console.log(`${this.name}${this.age}`)
                    }
                    const liLei = new Person('LiLei',20)
                    liLei.sayInfo()
                }
                commonWrite()

                //es6
                const es6Write = function () {
                    class Person{ //Person
                        constructor(name,age){ //constructor
                            this.name = name;  //this
                            this.age = age;
                        }
                        sayInfo(){
                            console.log(`${this.name}${this.age}`)
                        }
                    }
                    const liLei = new Person('LiLei',21)
                    liLei.sayInfo()
                }
                es6Write()

                //
                const classExtentTest = function () {
                    class Student {
                        constructor(name){
                            this.name = name
                        }
                        sayName(){
                            console.log(this.name)
                        }
                        testFn(){
                            console.log('')
                        }
                    }
                    class Worker extends Student{
                        sayWork(){
                            console.log(this.name)
                        }
                        testFn(){
                            console.log('')
                        }
                    }
                    const person = new Worker('liLei')
                    person.sayName()
                    person.sayWork()
                    person.testFn()
                    //
                    //liLei
                    //liLei
                    //
                }
                classExtentTest()

                const superTest = function () {
                    class Student {
                        constructor(name){
                            this.name = name
                        }
                        testFn(){
                            console.log('')
                        }
                    }
                    class Worker extends Student{
                        constructor(name,age,sex){
                            super(name)    //superthisage
                            this.age = age
                            this.sex = sex
                        }
                        testFn(){
                            super.testFn();
                            console.log(super.name)
                            console.log("" + this.age)
                            console.log("" + this.sex)
                            console.log('')
                        }
                    }
                    const person = new Worker('liLei','20')
                    person.testFn()
                    //
                    //
                    //20
                    //undefined
                    //
                    //
                }
                superTest()

                const staticText = function () {
                    class Person{      //constructorconstructor
                        static sayName(){
                            console.log("static")
                        }
                    }
                    class Student extends Person{}
                    const student = new Student()
                    Person.sayName()
                    Student.sayName()
                    // student.sayName()
                }
                staticText()

                const classUsed = function () {
                    class PageUtil{
                        constructor(pageNo,pageSize,total){    //
                            this.pageNo = pageNo;     //
                            this.pageSize = pageSize  //
                            this.total = total        //
                            this.currentPage = 0      //
                            this.pageTotal = Math.ceil(this.total/this.pageSize)   //
                        }
                        nextPage(){     //
                            if(this.currentPage < this.pageTotal){
                                this.currentPage++
                            }
                        }
                        beforePage(){    //
                            if(this.currentPage > 1){
                                this.currentPage--
                            }
                        }
                        jumpPage(page){     //
                            this.currentPage = page
                        }
                        changePageSize(pageSize){    //
                            this.pageSize = pageSize
                            this.pageTotal = Math.ceil(this.total/this.pageSize)   //
                        }
                        getTotalPage(){    //
                            return Math.ceil(this.total/this.pageSize)
                        }
                    }


                    class DialogPage extends PageUtil{    //PageUtil
                        constructor(pageNo,pageSize,total,pageTotal){
                            super(pageNo,pageSize,total)
                            this.pageTotal = pageTotal
                        }
                        getTotalPage(){
                            return this.pageTotal || super.getTotalPage()   //getTotalPage
                        }
                    }
                    const contentPage = new PageUtil(1,10,100)   //2pageUtil
                    contentPage.getTotalPage()
                    const dialogPage = new DialogPage(1,10,100,10)
                    dialogPage.getTotalPage()
                }
                classUsed()

            },
            getEditContent(){
